<template>
	<view class="container">
		<view class="address-list">
			<view class="a-address" v-for="(item,index) in addressList" :key="index">
				<view class="left-text" :class="item.isDefault? 'active':''" @tap="selectTap(item.id)">
					<view class="name-tel">
						{{item.label}}
					</view>
					<view class="address-box">
						{{item.address}}
					</view>
				</view>
				<view class="right-edit" @tap="editAddess(item.id)"></view>
			</view>
		</view>
		<view class="bottom-box">
			<view class="add-btn" @tap="addAddess">新增地址</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				addressList: ''
			}
		},
		onLoad() {
			this.init()
		},
		methods: {
			init() {
				var _self = this;
				uni.request({
					url: _self.$Url + '/address/get', //请求接口
					header: {
						'content-type': 'application/json; charset=UTF-8', //自定义请求头信息
						'refreshToken':uni.getStorageSync("refreshToken"),
						'token':uni.getStorageSync("token"),
					},
					method: 'POST',
					data: {
						userId:uni.getStorageSync("userId")
					},
					success: (res) => { // 请求成功后返回
						console.log(res)
						if (res.data.exceptionCode == 200) {
							this.addressList = res.data.result.addressList
						}
					}
				});
			},
			selectTap(id) {
				console.log("tap item id:" + JSON.stringify(id));
			},
			editAddess(id) {
				console.log("edit item id:" + id);
			},
			addAddess() {
				uni.navigateTo({
					url: '/pages/my/address/addAddress'
				})
			}
		}
	}
</script>

<style>
	.container {
		background-color: #F2f2f2;
	}

	.address-list {
		width: 100%;
		background-color: #fff;
		margin-top: 20upx;
		padding-bottom: 100upx;
	}

	.address-list .a-address {
		width: 720upx;
		margin-left: 30upx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 30upx 0;
		border-bottom: 1upx solid #eee;
	}

	.a-address .left-text {
		width: 610upx;
		box-sizing: border-box;
		padding-left: 58upx;
	}

	.a-address .left-text.active {
		background: url(data:image/png;base64,) no-repeat left center;
		background-size: 28upx auto;
	}

	.a-address .left-text .name-tel {
		margin-bottom: 20upx;
	}

	.a-address .left-text .address-box {
		font-size: 24upx;
		color: #888888;
		line-height: 36upx;
	}

	.a-address .right-edit {
		width: 109upx;
		height: 100%;
		padding: 50upx 0 50upx 58upx;
		box-sizing: border-box;
		background: url(data:image/png;base64,) no-repeat 43upx center;
		background-size: 35upx auto;
	}

	.bottom-box {
		position: fixed;
		width: 100%;
		bottom: 0;
		font-size: 28upx;
		color: #000000;
		border-top: 1upx solid #eee;
		height: 100upx;
		line-height: 100upx;
		background: #fff url(data:image/png;base64,) no-repeat 705upx center;
		background-size: 15upx auto;
	}

	.bottom-box .add-btn {
		margin-left: 30upx;
		padding-left: 70upx;
		background: url(data:image/png;base64,) no-repeat left center;
		background-size: 40upx auto;
	}
</style>
